.App {
  text-align: center;
}

.ProjectContainer{
 padding: 10px;
 text-align: center;
 width: 100%;
 height: 100%;
 display:                 -ms-flexbox;
 display:                 flex;
 display:                 -webkit-flex; /* Safari 8 */
 -ms-flex-wrap:               wrap;
     flex-wrap:               wrap;
 -webkit-flex-wrap:       wrap;         /* Safari 8 */
 -ms-flex-pack:         center;
     justify-content:         center;
 -webkit-justify-content: center;       /* Safari 8 */
}

.ProjectPanel{
  width: 20vw;
  height: 20vw;
  margin-top: 5px;
  margin-right: 5px;
  margin-left: 5px;
  margin-bottom: 5px;
  background-color: white;
  text-align: center;
  border-style: outset;
  border-width: 5px;
}

.PanelBackground{
  z-index: -100;
  max-width:100%;
  max-height:100%;
  width: 100%;
  height: 100%;
  display: block;
  opacity: 1;
}

.PanelHeader{
  color: black;
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  font-size: 40px;
  margin-top: 0px;
  margin-bottom: 15px;
  top: 10px;
  left: 0px;
  right: 0px;
  margin-top: 5px;
  margin-right: 5px;
  margin-left: 5px;
  margin-bottom: 5px;
  position: absolute;
}

.PanelDesc{
  color: black;
  font-family: 'Open Sans', sans-serif;
  margin-top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin-top: 5px;
  margin-right: 5px;
  margin-left: 5px;
  margin-bottom: 20px;
  position: absolute;
  font-weight: bold;
}

.ViewProjectsButton{
  margin-top: 30px;
  padding: 15px 25px;
  font-size: 24px;
  text-align: center;
  cursor: pointer;
  outline: none;
  color: #fff;
  background-color: #0099FF;
  border: none;
  border-radius: 15px;
  -webkit-box-shadow: 0 8px #0066FF;
          box-shadow: 0 8px #0066FF;
}

.ViewProjectsButton:hover {background-color: #00CCFF}

.ViewProjectsButton:active {
  background-color: #0099FF;
  -webkit-box-shadow: 0 5px #0066FF;
          box-shadow: 0 5px #0066FF;
  -webkit-transform: translateY(4px);
      -ms-transform: translateY(4px);
          transform: translateY(4px);
}

.ProjectPopup{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
}

.ProjectPopupInner{
  position: absolute;
  left: 10%;
  right: 10%;
  top: 10%;
  bottom: 10%;
  margin: auto;
  background: white;
  border-style: outset;
  border-width: 5px;
}

.LinkIcon{
  height: 50px;
  width: 50px;
  margin-right: 5px;
  margin-left: 5px;
}

.ProfileImage{
  margin-top: 50px;
  margin-right: 20px;
  margin-left: 20px;
  border-radius: 50%;
  width: 15%;
  height: 15%;
}

body {
  background-color: black;
}

h1,h2,h3,h4,h5,p{
  color: white;
  font-family: 'Open Sans', sans-serif;
}



